	module.exports = {"data":{"markdownRemark":{"html":"<p>Getting started is easy. All you need to do is click the <strong>Connect with STRAVA</strong> button in the upper right corner. You will be redirected to Strava to sign in and they will return you, logged in and ready to go.</p>\n<p>\n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 690px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 25.078043704474506%; position: relative; bottom: 0; left: 0; background-image: url('data:image/png;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Menu Drawer\"\n        title=\"\"\n        src=\"/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-2f32e.png\"\n        srcset=\"/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-544f6.png 173w,\n/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-d08f6.png 345w,\n/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-2f32e.png 690w,\n/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-056b2.png 1035w,\n/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-59cb9.png 1380w,\n/blog/static/how-do-i-get-started-01-1a9cca45c17ee751ff3c8d6976bfb7bd-b5a09.png 1922w\"\n        sizes=\"(max-width: 690px) 100vw, 690px\"\n      />\n    </span>\n  </span>\n  \nOnce inside, use the icon in the upper left to open the Menu Drawer.</p>\n<h3>Advanced features, such as adding new events, also requires membership to the A Race athlete Strava club (<a href=\"https://www.strava.com/clubs/araceathlete\">here</a>).</h3>","frontmatter":{"title":"How do I get started?","date":"2017-06-02T00:21:00.000Z","path":"/how-do-i-get-started/","parent":"/home/hypersprite/Documents/code/araceathlete-gatsby-material-ui-v1/src/pages/2017-06-02-how-do-i-get-started/index.md absPath of file","tags":"newbie"}}},"pathContext":{"slug":"/2017-06-02-how-do-i-get-started/","_PARENT":"SOURCE"}}

